@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@600&display=swap);
*{
  margin: 0px;
  padding: 0px;
}

body {
  margin: 0;
  font-family: "Poppins", -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.signInBtn{
  background-color: red;
  max-width: 150px;
  text-align: center;
  padding: 8px 16px;
  border-radius: 4px;
  color: white;
  font-size: 14px;
  font-weight: 500;
}

.navbar {
  display: flex;
  justify-content: space-between;
  padding: 12px 24px;
  background-color: white;
}

.home {
  background-color: #f3f2ef;
  height: 100vh;
}

.createPost {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 24px 24px;
}

.navbar__img{
  height: 35px;
  border-radius: 8px;
}

.createPost__loggedIn{
  background-color: white;
  width: 100%;
  max-width: 600px;
  border-radius: 4px;
  padding: 12px 16px;
}

/* grab the input child of this class */
.createPost__imageUpload > input{
  /* display=none will get rid of "choose file" in the input */
  display: none; 
}

.createPost__uploadBtn{
  border: none;
  background-color: white;
}

.createPost__loggedInBottom{
  display: flex;
  justify-content: space-between;
}

.createPost__textarea {
  border: none;
  width: 100%;
  /* get rid of outline on textare */
  outline: none;
  font-size: 14px;
  margin-top: 6px;
  /* apply my font to textare */
  font-family: "Poppins", -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
  'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
  sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

/* get rid of outline on textare */
.createPost__textarea:focus {
  outline: none;
}

.createPost__imagePreview{

}

#image-preview {
  display: none;
  height: 80px;
  margin: 12px 0px;
  border-radius: 4px;
}

.feed {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.post {
  background-color: white;
  max-width: 600px;
  width: 90vw;
  padding: 16px;
}

.post__header{
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.post__headerLeft{
  display: flex;
}

.post__profilePic{
  height: 35px;
  border-radius: 8px;
}

.post__delete{
  border: none;
  background-color: white;
  cursor: pointer;
}

.post__delete:focus{
  outline: none;
}

.post__photoUrl{
  width: 100%;
  /* so img doesnt stretch w/ changing viewport */
  /* object-fit: cover; */
  margin: 16px 0px;
}

.commentInput{
  display: flex;
  justify-content: space-between;
  font-family: "Poppins", -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
  'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
  sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.commentInput_textarea{
  border: none;
  width: 100%;
  resize: none;
  margin: 8px 0px;
  font-size: 14px;
  /* apply my font to textarea */
  font-family: "Poppins", -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
  'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
  sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.commentInput_textarea:focus{ 
  outline: none;
}

.commentInput_btn{
  background-color: white;
  border: none;
}

.commentInput_btn:focus{
  outline: none;
}
